
@forward 'hero';
@forward 'gallery';
@forward 'footer';
@forward 'location';



.flex {
    display: flex;
    gap: var(--gap, 1rem);
}

.grid-container {
    display: grid;
    grid-gap: var(--grid-gap, 1rem)
}

.container {
    max-width: 90rem;
    margin: 0 auto;
}

.flow > * + * {
     margin-top: var(--flow-space, 1rem);
 }

.uppercase {
    text-transform: uppercase;
}

